.wrap{
    overflow: auto hidden;
    position: relative;
    /* width: 800px; */
    height: 128px;
    margin: 0 auto;
    
}
.cover{
    overflow: hidden;
    position: relative;
    height: 100%;
    width: 0;
    box-sizing: border-box;
    transform: translateZ(0);
    z-index: 2;
    border-right: 1px solid #37A2DF;
}
.wrap .region{
    cursor: move;
    position: absolute;
    z-index: 3;
    height: 100%;
    width: 100px;
    top: 0;
    box-sizing: border-box;
    border-left: 1px solid #000;
    border-right: 1px solid #000;
    background-color: rgba(0, 0, 0, 0.1);
} 
.cover canvas{
    z-index: 3;
}
.wrap canvas{
    position: absolute;
    overflow: hidden;
    left: 0;
    top: 0;
    bottom: 0;
    height: 100%;
}
button{
    margin-right: 10px;
}